@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:400,700|Montserrat:400,700|Poppins:400,700|Roboto:400,700&display=swap&subset=latin-ext);
* {
	box-sizing: border-box !important;
}

.main_container_login {
	/* background-color: #9b59b6 !important; */
	display: flex  !important;
	align-items: center !important;
	justify-content: center !important;
	min-height: 90vh !important;
	margin: 0 !important;
}
.container_login {
	background-color: #fff !important;
	border-radius: 5px  !important;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3) !important;
	overflow: hidden !important;
	width: 400px !important;
	max-width: 100% !important;
}

.header {
	/* border-bottom: 1px solid #f0f0f0; */
	/* background-color: #f7f7f7; */
	padding: 20px 40px !important;
}

.header h2 {
	margin: 0 !important;
}

.form_login {
	padding: 30px 40px !important;
}

.form-control_login {
	margin-bottom: 10px !important;
	padding-bottom: 20px !important;
	position: relative !important;
}

.form-control_login label {
	display: inline-block !important;
	margin-bottom: 5px !important;
}

.form-control_login input {
	border: 2px solid #f0f0f0 !important;
	border-radius: 4px !important;
	display: block !important;
	font-family: inherit !important;
	font-size: 14px !important;
	padding: 10px !important;
	width: 100% !important;
}

.form-control_login input:focus {
	outline: 0 !important;
	border-color: #777 !important;
}



.form_login button {
	background-color: #117dc5 !important;
	border: 2px solid #117dc5!important;
	border-radius: 4px !important;
	color: #fff !important;
	display: block !important;
	font-family: inherit !important;
	font-size: 16px !important;
	padding: 10px !important;
	margin-top: 20px !important;
	width: 100% !important;
}


html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #efefef;
}
a {
  -webkit-transition: all 0.5 ease-in-out;
  transition: all 0.5 ease-in-out;
}

a:hover {
  text-decoration: none;

}

#card_ant {
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  align-items: center;
  font-family: 'apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji' !important;
}

#card_pdf_preview {
  -webkit-transition: all 0.1s ease-in-out !important;
  transition: all 0.1s ease-in-out !important;

  justify-self: self-end !important;
  margin-right: 5px !important;
  -webkit-transform: scale(1.3) !important;
          transform: scale(1.3) !important;
  margin-top: 110px !important;
  overflow: hidden;

  font-family: 'apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji' !important;
}

#card_pdf:hover,
#card_ant_film:hover,
.ant-card.ant-card-bordered {
  box-shadow: 1px 1px 20px 5px rgba(0, 0, 0, 0.75);
}

#card_film_preview {
  -webkit-transition: all 0.1s ease-in-out !important;
  transition: all 0.1s ease-in-out !important;

  justify-self: self-end !important;
  margin-right: 5px !important;
  -webkit-transform: scale(1.1) !important;
          transform: scale(1.1) !important;
  margin-top: 30px !important;
  overflow: hidden;

  font-family: 'apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji' !important;
}

.ant-form.ant-form-horizontal {
  justify-self: self-start;
  padding: 50px;
  padding-left: 0;
  padding-right: 130px;
  margin-left: 0;
  width: 100%;
}
.ant-col.ant-form-item-label.ant-col-xs-24.ant-col-sm-8 {
  margin: 0;
}
.ant-row.ant-form-item {
  margin: 0 0 9px 0 !important;
}

.burger {
  background: none;
  border: none;
  padding: 0;
  font-size: 24px;
  -webkit-transform: scaleX(1.5);
          transform: scaleX(1.5);
  display: none;
}

ul li {
  opacity: 0.7;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
}
ul li:hover {
  opacity: 1;
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.animated.fadeIn {
  max-width: 100% !important;
}

#type_cards_wrapper {
  margin-bottom: 60px !important;
}

input[type='file'] {
  height: 1px;
}

label[name='Wybierz plik'] {
  /* margin-top: 40px; */
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  cursor: pointer;
}

.progress{
 -webkit-transform: translateY(20px);
         transform: translateY(20px);
 border: 1px solid rgb(206, 206, 206);
}

#card_ant_film .ant-card-bod,
#card_film_preview .ant-card-body {
  height: 60px;
  padding: 20px;
  padding-right: 35px;
}


/* !! home buttons */

#home_container {
  display: flex !important;
  flex-wrap: wrap !important;
  justify-content: space-evenly !important;
  align-items: center !important;
  align-content: center !important;
  height: 80vh !important;
}

.home_btn {
  box-shadow: 1px 1px 10px -4px rgba(0, 0, 0, 0.75) !important;
  -webkit-transition: all 0.1s ease-in-out !important;
  transition: all 0.1s ease-in-out !important;
  margin: 15px !important;
  width: 325px !important;
}

.home_btn:hover {
  box-shadow: 1px 1px 20px -4px rgba(0, 0, 0, 0.75) !important;
  cursor: pointer !important;

  -webkit-transform: translateY(-1px) !important;

          transform: translateY(-1px) !important;
}

#home_button_film {
  width: 360px !important;
}

#home_button_pdf {
  width: 280px !important;
}

@media (max-width: 768px) {
  .burger {
    display: inline-block;
  }
  ul {
    /* display: none !important; */
    flex-direction: column;
    width: 100%;
  }
  nav {
    flex-wrap: wrap !important;
    text-align: center;
  }
  ul li {
    /* display:none; */
    margin: 15px;
  }

  div[style='display: grid; grid-template-columns: 30% 70%; width: 100%;'],
  div[style='display: grid; grid-template-columns: 40% 60%; width: 100%;'] {
    display: flex !important;
    flex-wrap: wrap !important;
    justify-content: center !important;
    align-items: center !important;
  }

  #card_pdf_preview {
    -webkit-transform: scale(1) !important;
            transform: scale(1) !important;
    margin-top: 0px !important;
  }

  .ant-form.ant-form-horizontal {
    justify-self: self-start;
    padding: 10px;
    margin-left: 0;
    width: 90%;
  }
  .ant-col.ant-form-item-label.ant-col-xs-24.ant-col-sm-8,
  .ant-form-item-required {
    padding: 0 !important;
    margin: 0 !important;
  }
  .ant-row.ant-form-item {
    margin: 0 0 7px 0 !important;
  }

  #card_pdf {
    display: block !important;
    margin: 0 auto !important;
  }
  h5 {
    text-align: center !important;
  }

#home_button_film {
  width: 300px !important;
}

#home_button_pdf {
  width: 220px !important;
}
#card_ant_pdf{
margin-left: 13vw !important;
}

}

@media (min-width: 769px) {
  ul {
    display: flex !important;
  }
}

